
.textfield {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  height: 28px;
  width: 200px;
}
.textfield .textfield-input {
  width: 100%;
  height: 100%;
  border: 1px solid #ccc;
  padding: 5px 0 5px 8px;
  color: #234156;
  background: #fff;
  outline: 0;
}
.textfield .textfield-input:focus,
.textfield .textfield-input.focus {
  border-color: #17c1c5;
  background-color: #fff;
}
.textfield .textfield-input.invalid {
  border-color: #fc6969;
  background-color: #fffcfc;
}
.textfield .textfield-input::-webkit-input-placeholder,
.textfield .textfield-input:-moz-placeholder,
.textfield .textfield-input::-moz-placeholder,
.textfield .textfield-input::-ms-input-placeholder {
  color: #7b7b7b;
}
.textfield .textfield-input[disabled="disabled"] {
  background-color: #efefef;
}
[trigger-type]:not([trigger-type="text"]) .textfield-input {
  padding-right: 30px;
}
.textfield-trigger {
  position: absolute;
  right: 5px;
  top: 50%;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  border: none;
  outline: 0;
  padding: 0;
  background: transparent no-repeat 100% 0;
  -webkit-transition: none 0.25s ease-out;
  transition: none 0.25s ease-out;
  -webkit-transition-property: background, color, margin;
  transition-property: background, color, margin;
}
.textfield-trigger:hover,
.textfield-trigger:focus {
  color: #17c1c5;
}
.textfield-trigger:active {
  margin-top: -9px;
}


