.layer {
  position: absolute;
  border-radius: 4px;
  background: #fff;
  border: 1px solid #ccc;
  color: #000;
  font-size: 12px;
  line-height: 1.2;
  opacity: 1;
}
.layer[x-placement^="top"] {
  margin-bottom: 0;
}
.layer[x-placement^="top"].layer-with-arrow {
  margin-bottom: 10px;
}
.layer[x-placement^="right"] {
  margin-left: 0;
}
.layer[x-placement^="right"].layer-with-arrow {
  margin-left: 10px;
}
.layer[x-placement^="bottom"] {
  margin-top: 0;
}
.layer[x-placement^="bottom"].layer-with-arrow {
  margin-top: 10px;
}
.layer[x-placement^="left"] {
  margin-right: 0;
}
.layer[x-placement^="left"].layer-with-arrow {
  margin-right: 10px;
}
.layer .layer-header {
  display: inline-block;
  width: 100%;
  border-bottom: 1px solid #ccc;
  padding: 10px 12px;
}
.layer .layer-header .layer-title {
  font-size: 14px;
}
.layer .layer-body {
  width: 100%;
  height: 100%;
}
.layer .layer-close {
  position: absolute;
  font-size: 16px;
  right: 10px;
  top: 8px;
  cursor: pointer;
  z-index: 10;
}
.layer-overlay {
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  opacity: 0;
  background: #fff;
}
.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  opacity: 1;
  -webkit-transition: opacity 200ms linear;
  transition: opacity 200ms linear;
}
.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0;
}


