
.layer.tooltip .layer-arrow:before,
.layer.tooltip .layer-arrow:after {
  content: '';
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  border-width: 6px;
}
.layer.tooltip[x-placement="top-start"] .layer-arrow:before {
  border-top-color: #000;
  bottom: -13px;
  left: 6px;
}
.layer.tooltip[x-placement="top-start"] .layer-arrow:after {
  border-top-color: #000;
  bottom: -12px;
  left: 6px;
}
