
.scrollbar {
  position: relative;
  margin: 0 auto;
  overflow: hidden;
  height: 100%;
  width: 100%;
}
.scrollbar:hover .scrollbar-v,
.scrollbar:hover .scrollbar-h {
  background-color: rgba(0, 0, 0, .2);
}
.scrollbar-body {
  position: absolute;
  transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  min-width: 100%;
}


