
.sf-datepicker {
  width: 230px;
  height: 250px;
  padding: 12px;
  text-align: center;
  border: 1px solid #ccc;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.sf-datepicker .datepicker-header {
  position: relative;
}
.sf-datepicker .datepicker-header i.left-caret-icon {
  width: 20px;
  height: 20px;
  font-size: 20px;
  cursor: pointer;
  position: absolute;
  top: 4px;
  left: 4px;
}
.sf-datepicker .datepicker-header i.right-caret-icon {
  width: 20px;
  height: 20px;
  font-size: 20px;
  cursor: pointer;
  position: absolute;
  top: 4px;
  right: 4px;
}
.sf-datepicker .datepicker-week-ceil {
  display: inline-block;
  width: 28px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  cursor: pointer;
  font-family: '微软雅黑';
  margin-bottom: 5px;
}
.sf-datepicker .datepicker-date-ceil {
  display: inline-block;
  width: 28px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  cursor: pointer;
}
.sf-datepicker .datepicker-date-ceil.other,
.sf-datepicker .datepicker-date-ceil.over {
  color: #ccc;
}
.sf-datepicker .datepicker-date-ceil.between {
  background: #ebf4f8;
}
.sf-datepicker .datepicker-date-ceil:hover {
  background: #eee;
}
.sf-datepicker .datepicker-date-ceil.active {
  background: #17c1c5;
  color: #fff;
}


