
.sf-option {
  font-size: 12px;
  padding: 8px 10px;
  position: relative;
  color: #475669;
  height: 28px;
  box-sizing: border-box;
  cursor: pointer;
}
.sf-option:hover {
  background-color: #cfc;
}
.sf-option[disabled="disabled"] {
  color: #aaa;
  cursor: default;
}
.sf-option.active:not([disabled]) {
  background-color: #00a1ff;
  color: #fff;
}


