
.pagination {
  margin: 0;
  display: block;
  padding: 6px;
  height: 42px;
  background-color: #fff;
  border-radius: 0px;
  border: solid 1px #ddd;
  border-top: none;
}
.pagination-total-text {
  float: left;
  margin-right: 10px;
  line-height: 30px;
  color: #999;
  font-size: 12px;
}
.pagination-nav {
  padding: 0;
  margin: 0;
  height: 30px;
  border: 1px solid #ddd;
  float: left;
}
.pagination-nav-item {
  float: left;
  cursor: pointer;
  border-left: 1px solid #ddd;
  list-style-type: none;
  color: #666;
  height: 28px;
  line-height: 28px;
  text-align: center;
  padding: 0 2px;
  min-width: 30px;
  background-repeat: no-repeat;
}
.pagination-nav-item .fa {
  color: #000;
}
.pagination-nav-item:first-child {
  border-left: none;
}
.pagination-nav-item-selected {
  color: #fff;
  background-color: #22b07b;
}
.pagination-nav-item-disabled {
  cursor: default;
}
.pagination-nav-item-disabled .fa {
  color: #ccc;
}


