
.sf-mask {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  z-index: 10000;
  background-color: rgba(23, 193, 197, .6);
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
/*for ie10*/
  display: -ms-flexbox;
  -ms-flex-pack: center; /*水平居中*/
  -ms-flex-align: center; /*垂直居中*/
  -ms-flex-direction: column;
}
.sf-mask-position {
  position: relative;
  z-index: 0;
}
.sf-mask-unselectable {
  user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
  -moz-user-select: -moz-none;
  -webkit-user-select: none;
  cursor: default;
}
.sf-mask-msg {
  text-align: center;
  padding-top: 10px;
}
.sf-mask-msg,
.sf-mask-loading {
  color: #fff;
  display: block;
  overflow: visible;
}
.sf-mask-loading,
.sf-mask-loading > em {
  width: 45px;
  height: 45px;
}
.sf-mask-loading > em {
  position: absolute;
  opacity: 0;
  display: block;
  -webkit-transform: rotate(225deg);
  transform: rotate(225deg);
  -webkit-animation-name: sf-mask-loading;
  animation-name: sf-mask-loading;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-duration: 5.28s;
  animation-duration: 5.28s;
}
.sf-mask-loading > em:after {
  content: "";
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #fff;
}
.sf-mask-loading > em:nth-child(2) {
  -webkit-animation-delay: 0.23s;
  animation-delay: 0.23s;
}
.sf-mask-loading > em:nth-child(3) {
  -webkit-animation-delay: 0.46s;
  animation-delay: 0.46s;
}
.sf-mask-loading > em:nth-child(4) {
  -webkit-animation-delay: 0.69s;
  animation-delay: 0.69s;
}
.sf-mask-loading > em:nth-child(5) {
  -webkit-animation-delay: 0.92s;
  animation-delay: 0.92s;
}
@-webkit-keyframes sf-mask-loading {
0% {
    -webkit-transform: rotate(225deg);
    opacity: 1;
    -webkit-animation-timing-function: ease-out;
}
8% {
    -webkit-transform: rotate(345deg);
    -webkit-animation-timing-function: linear;
}
30% {
    -webkit-transform: rotate(455deg);
    -webkit-animation-timing-function: ease-in-out;
}
40% {
    -webkit-transform: rotate(690deg);
    -webkit-animation-timing-function: linear;
}
60% {
    -webkit-transform: rotate(815deg);
    opacity: 1;
    -webkit-animation-timing-function: ease-out;
}
75% {
    -webkit-transform: rotate(965deg);
    -webkit-animation-timing-function: ease-out;
}
76% {
    opacity: 0;
}
100% {
    opacity: 0;
}
}
