
.sf-menu-list-item {
  padding: 0 24px 0 12px;
  cursor: pointer;
  position: relative;
  height: 30px;
  line-height: 30px;
}
.sf-menu-list-item:hover {
  background-color: #474a4a;
}
.sf-menu-list-item[disabled="disabled"] {
  opacity: 0.3;
  cursor: default;
  background: transparent;
}
.sf-menu-list-item .sf-menu-icon-before {
  margin-right: 6px;
  vertical-align: middle;
}
.sf-menu-list-item .sf-menu-icon-after {
  position: absolute;
  right: 8px;
  top: calc(50% - 15px);
}
.sf-menu-list-item .sf-menu-separator {
  height: 1px;
  background-color: #49525b;
  padding: 0;
  background-image: none;
  width: auto;
}


