
.datefield {
  display: inline-block;
}
.layer.datefield-layer {
  padding: 0;
}
.sf-datepicker.datefield-picker,
.sf-datepicker-range.datefield-picker {
  border: none;
}
.datefield-picker-btns {
  border-top: 1px solid #ccc;
  padding: 8px 18px;
  text-align: right;
}


