
.sf-menu-layer {
  border-radius: 0;
  padding: 0;
  background: #3c434b;
  color: #fff;
  border: none;
}
.sf-menu-layer[x-placement^="top"] {
  margin-bottom: 0;
}
.sf-menu-layer[x-placement^="bottom"] {
  margin-top: 0;
}
.sf-menu-layer[x-placement^="left"] {
  margin-right: 0;
}
.sf-menu-layer[x-placement^="right"] {
  margin-left: 0;
}
.sf-menu-layer .sf-menu-scroll.scrollbar:hover .scrollbar-horizontal-inner {
  background: #ccc;
}
.sf-menu-layer .sf-menu-scroll.scrollbar:hover .scrollbar-vertical-inner {
  background: #ccc;
}
.sf-menu-layer .sf-menu-list {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  box-sizing: border-box;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.sf-menu-layer .sf-menu-list .sf-menu-item-context {
  display: inline-block;
}
.sf-menu-layer .sf-menu-separator {
  height: 1px;
  background-color: #49525b;
  padding: 0;
  background-image: none;
  width: auto;
}


