
.sf-panel {
  background: #fff;
  border: solid 1px #ddd;
}
.sf-panel .sf-panel-header {
  height: 44px;
}
.sf-panel .sf-panel-tool {
  padding: 12px 12px 12px 0;
}
.sf-panel .sf-panel-title {
  padding: 12px;
  font-size: 14px;
  font-weight: bold;
}
.sf-panel .sf-panel-body {
  padding: 0;
  height: calc(100% - 44px);
}
.sf-panel .sf-panel-body-inner {
  padding: 12px;
}
.sf-panel-none-header .sf-panel-body {
  height: 100%;
}


