
.sf-modal-wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.sf-modal {
  background: #fff;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-width: 400px;
  max-width: 800px;
  min-height: 180px;
  top: 50%;
  left: 50%;
  position: fixed;
  border: 1px solid #17c1c5;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
}
.sf-modal .sf-modal-header {
  padding: 15px 20px;
  border-bottom: 1px solid #e5e5e5;
  width: 100%;
  top: 0;
  font-size: 14px;
}
.sf-modal .sf-modal-header .fa-close {
  font-size: 16px;
  cursor: pointer;
}
.sf-modal .sf-modal-body {
  min-height: 70px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  overflow: auto;
}
.sf-modal .sf-modal-footer {
  height: 60px;
  border-top: 1px solid #e5e5e5;
  padding: 15px;
  width: 100%;
  bottom: 0;
}
.sf-modal .sf-modal-button {
  display: inline-block;
  margin: 0 6px;
}
.sf-modal-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000;
  opacity: 0.6;
}


