
.sf-select {
  position: relative;
  display: inline-block;
  line-height: 1.5;
}
.sf-select .sf-select-placeholder {
  color: #aaa !important;
}
.sf-select .sf-select-display {
  position: relative;
  width: 100%;
  height: 28px;
  line-height: 18px;
  text-align: left;
  display: inline-block;
  padding: 4px 30px 4px 8px;
  font-size: 12px;
  color: #333;
  background-color: #fff;
  border-color: #ccc;
  font-weight: 400;
  white-space: nowrap;
  vertical-align: middle;
  border: 1px solid #ccc;
}
.sf-select .sf-select-display i.option-icon {
  width: 16px;
  font-size: 14px;
  margin-right: 6px;
}
.sf-select .sf-select-display i.arrow-icon {
  position: absolute;
  right: 8px;
  top: 5px;
}
.sf-select .sf-select-overlay {
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
  left: 0;
  top: 0;
  outline: none;
  border: none;
  cursor: pointer;
}
.sf-select .sf-select-overlay:focus {
  border: 1px solid #17c1c5;
}
.sf-select .sf-select-overlay[disabled="disabled"] {
  background: #eee;
  opacity: 0.5;
  cursor: default;
}
.sf-options-layer {
  background: #fff;
  color: #000;
  padding: 0;
  border-radius: 0;
  max-height: 300px;
  border: none;
  text-align: left;
}
.sf-options-layer[x-placement^="top"] {
  margin-bottom: 0;
}
.sf-options-layer[x-placement^="bottom"] {
  margin-top: 0;
}
.sf-options-layer[x-placement^="left"] {
  margin-right: 0;
}
.sf-options-layer[x-placement^="right"] {
  margin-left: 0;
}
.sf-options-layer .sf-options-list {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}
.sf-options-layer .sf-options-list i.option-icon {
  width: 16px;
  font-size: 14px;
  margin-right: 6px;
}
.sf-options-layer .sf-select-empty {
  width: 100%;
  height: 75px;
  padding: 20px 0;
  text-align: center;
}


